@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-4px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(-8px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
.animate-fadeIn {
  animation: fadeIn 0.3s ease-out;
}
.animate-slideIn {
  animation: slideIn 0.3s ease-out;
}
.animate-scaleIn {
  animation: scaleIn 0.2s ease-out;
}
.gradient-border-wrapper {
  position: relative;
  border-radius: 12px;
  padding: 2px;
  background: linear-gradient(90deg, #8b5cf6, #ec4899, #f97316, #ec4899, #8b5cf6);
  background-size: 200% 100%;
  animation: gradient-shift 4s ease infinite;
}
.gradient-border-wrapper::before {
  content: '';
  position: absolute;
  inset: -2px;
  border-radius: 14px;
  background: linear-gradient(90deg, #8b5cf6, #ec4899, #f97316, #ec4899, #8b5cf6);
  background-size: 200% 100%;
  animation: gradient-shift 4s ease infinite;
  filter: blur(8px);
  opacity: 0.5;
  z-index: -1;
}
.gradient-border-inner {
  background: var(--grafana-color-background-primary);
  border-radius: 10px;
  position: relative;
  z-index: 1;
}
@keyframes gradient-shift {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes sparkle {
  0%,
  100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.8;
    transform: scale(1.1);
  }
}
.animate-sparkle {
  animation: sparkle 2s ease-in-out infinite;
}
.status-badge {
  display: inline-flex;
  align-items: center;
  padding: 6px 14px;
  border-radius: 4px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  border: 1px solid var(--grafana-color-warning);
  color: var(--grafana-color-warning);
  background: transparent;
}
.suggestion-pill {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 13px;
  background: var(--grafana-color-background-secondary);
  border: 1px solid var(--grafana-color-border-weak);
  color: var(--grafana-color-text-primary);
  cursor: pointer;
  transition: all 0.2s ease;
}
.suggestion-pill:hover {
  background: var(--grafana-color-background-canvas);
  border-color: var(--grafana-color-border-medium);
  transform: translateY(-1px);
}
.assistant-badge {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 500;
  background: rgba(139, 92, 246, 0.15);
  color: #a78bfa;
  border: 1px solid rgba(139, 92, 246, 0.3);
}
